.flicking-viewport {
    position: relative;
    overflow: hidden;
}
.flicking-viewport.vertical {
    display: inline-flex;
}
.flicking-viewport.vertical > .flicking-camera {
    display: inline-flex;
    flex-direction: column;
}
.flicking-viewport.flicking-hidden > .flicking-camera > * {
    visibility: hidden;
}

.flicking-camera {
    width: 100%;
    height: 100%;
    display: flex;
    position: relative;
    flex-direction: row;
    z-index: 1;
    will-change: transform;
}
.flicking-camera > * {
    -ms-flex-negative: 0;
    flex-shrink: 0;
}
